import {
  loginUserCall,
  signUpCall
} from './AuthActions';

export const loginUser = ({ email, password }) => {
	return loginUserCall({ email, password });
};

export const signupUser = (firstName, lastName, email, password, age, country, profileImage) => {
	return signUpCall(firstName, lastName, email, password, age, country, profileImage);
};



