export const signUpCall = (firstName = '', lastName = '', email = '', password = '', age = '', country = '', profilePic = '') => {
  return dispatch => {
    dispatch({ type: SIGNUP });


    
    axios.post(BASE_URL, JSON.stringify({"ServiceCommand": "SignupCommand",
   "email": email,
   "destination service": "Users",
   "pass": password,
    "age": age,
    "country": country,
    "first_name": firstName,
   "last_name": lastName,
   "profile_pic": profilePic}))
    .then((response) => {
     if (!response.success) { 
        console.log(response);
        return loginFail(dispatch, response.error); 
        
      }

      // const user = data.user;
      // user.token = data.token;

      dispatch(loginUserCall({ email, password }));
    })
    .catch(error => { loginFail(dispatch, 'Sign up failed'); });
  };
};


export const editCall = (firstName = '', lastName = '', email = '', password = '', age = '', country = '', profilePic = '') => {
  return dispatch => {
    dispatch({ type: EDIT });
    //find id and token
    axios.post(BASE_URL, JSON.stringify({"ServiceCommand": "UpdateUserCommand",
   "email": email,
   "destination service": "Users",
   "pass": password,
    "age": age,
    "country": country,
    "first_name": firstName,
   "last_name": lastName,
   "profile_pic": profilePic}))
    .then((response) => {
     if (!response.success) { 
        console.log(response);
        return loginFail(dispatch, response.error); 
        
      }

      // const user = data.user;
      // user.token = data.token;

      dispatch(loginUserCall({ email, password }));
    })
    .catch(error => { loginFail(dispatch, 'Sign up failed'); });
  };
};



const loginFail = (dispatch, error) => {

   console.log("111");
  dispatch({ type: LOGINFAIL, payload: error });
};

const logInUser = (dispatch, user) => {

  console.log("111");
  dispatch({ type: LOGIN, payload: user });
  //the user is contains only the access token and user_id
};


