import React, { Component } from 'react';
import { Link } from 'react-router-dom'
import Img from 'react-image'
import './css/Userhome.css'

import { Input, Button, FormGroup,Form, FormFeedback, Label, Col, Media } from 'reactstrap';
class Userhome extends Component {
 state = {
    email: false,
    password: false,
    first_name: false,
    last_name: false,
    gender: false
  };

   constructor() {
    super();
    this.emailClicked = this.emailClicked.bind(this);
    this.passwordClicked = this.passwordClicked.bind(this);
    this.firstnameClicked = this.firstnameClicked.bind(this);
    this.lastnameClicked = this.lastnameClicked.bind(this);
    this.genderClicked = this.genderClicked.bind(this);
  }

 emailClicked() {
    const { email } = this.state;
    if (email) {
      this.setState({email: false});
    } else {
      this.setState({password: false, email: true, first_name: false, last_name: false, gender: false});
    }
  }

firstnameClicked(){
 const { first_name } = this.state;
    if (first_name) {
      this.setState({first_name: false});
    } else {
      this.setState({password: false, email: false, first_name: true, last_name: false, gender: false});
    }
}

lastnameClicked() {
 const { last_name } = this.state;
    if (last_name) {
      this.setState({last_name: false});
    } else {
      this.setState({password: false, email: false, first_name: false, last_name: true, gender: false});
    }
}

genderClicked() {
 const { gender } = this.state;
    if (gender) {
      this.setState({gender: false});
    } else {
      this.setState({password: false, email: false, first_name: false, last_name: false, gender: true});
    }
}

 passwordClicked() {
    const { password } = this.state;
    if (password) {
      this.setState({password: false});
    } else {
      this.setState({password: true, email: false, first_name: false, last_name: false, gender: false});
    }
  }

  renderEditEmail(){
        if (this.state.email) {
        return (<FormGroup row style = {style.inputStyle}>
          <Col sm={10}>
          <Input placeholder="Edit email" className="text-center " />
          </Col>
          <Button>Update</Button>
        </FormGroup>);
      }
  }
  renderEditFirstname(){
        if (this.state.first_name) {
        return (<FormGroup row style = {style.inputStyle}>
          <Col sm={10}>
          <Input placeholder="Edit first name" className="text-center " />
          </Col>
          <Button>Update</Button>
        </FormGroup>);
      }
  }

  renderEditLastname(){
        if (this.state.last_name) {
        return (<FormGroup row style = {style.inputStyle}>
          <Col sm={10}>
          <Input placeholder="Edit last name" className="text-center " />
          </Col>
          <Button>Update</Button>
        </FormGroup>);
      }
  }


  renderEditgender(){
        if (this.state.gender) {
        return (<FormGroup row style = {style.inputStyle}>
          <Col sm={10}>
          <Input placeholder="Edit gender" className="text-center " />
          </Col>
          <Button>Update</Button>
        </FormGroup>);
      }
  }
  renderEditPassword() {
        if (this.state.password) {
        return (<FormGroup row style = {style.inputStyle}>

          <Col sm={10}>
          <Input placeholder="Edit password" className="text-center " />
          </Col>

          <Button>Update</Button>
        </FormGroup>);
      }
  }

 render() {
return (
  <div style = {{minHeight: '100vh', backgroundColor : '#fafafa'}}>
        <Form>
        <FormGroup row style = {style.inputStyle}>
          <Img id = "image"  src= "https://www.w3schools.com/images/w3schools_green.jpg" style = {style.imageStyle}/>
        </FormGroup>
        <FormGroup row style = {style.inputStyle}>
          <Label className="align-center " for="email" sm={2} >Email</Label>
          <Col sm={10}>
          <Button  style = {style.buttonStyle}  bsSize="lg" onClick = {this.emailClicked}>Abdo@gmail.com</Button>
          </Col>
        </FormGroup>
        {this.renderEditEmail()}
        <FormGroup row style = {style.inputStyle}>

          <Label className="align-center" for="password" sm={2} >Password</Label>
          <Col sm={10}>
          <Button  style = {style.buttonStyle}  bsSize="lg" onClick = {this.passwordClicked}>**********</Button>
          </Col>
        </FormGroup>
        {this.renderEditPassword()}
         <FormGroup row style = {style.inputStyle}>

          <Label className="align-center"  sm={2} for="first_name">Firstname</Label>
          <Col sm={8}>
          <Button  style = {style.buttonStyle}  bsSize="lg" onClick = {this.firstnameClicked}>Abdo</Button>
          </Col>
        </FormGroup>
        {this.renderEditFirstname()}
        <FormGroup row style = {style.inputStyle}>

          <Label className="align-center"  sm={2} for="last_name">Lastname</Label>
          <Col sm={8}>
          <Button  style = {style.buttonStyle}  bsSize="lg" onClick = {this.lastnameClicked}>Rustom</Button>
          </Col>
        </FormGroup>
        {this.renderEditLastname()}
        <FormGroup row style = {style.inputStyle}>

          <Label className="align-center"  sm={2} for="gender">Gender</Label>
          <Col sm={8}>
          <Button  style = {style.buttonStyle}  bsSize="lg" onClick = {this.genderClicked}>Male</Button>
          </Col>
        </FormGroup>
        {this.renderEditgender()}
      </Form>
  </div>
  );
}
}

const style = {
  
  inputStyle: {
    width: 500, 
    marginLeft:400
  },
  buttonStyle: {
    backgroundColor : '#ffffff',
    color: '#000000',
    width: 400
  },
  imageStyle: {
   marginLeft: 230,
   marginTop: 30
  }

}
export default Userhome ;

